import React, { useEffect, useState, useRef } from 'react';
import { songs } from '../utils/Notes.js';
import { getStoredChordName } from "../utils/Funcs";
import {Button, Container, MenuItem, Select, Slider, Typography, useTheme} from "@mui/material";

const MusicPlayer = ({ setCurrentNoteIndex, chordPlayer }) => {
    const pauseBetweenNotes = 200; // 100 milliseconds

    const [ sequence, setSequence ] = useState("clockwise");
    const [audioContextStarted, setAudioContextStarted] = useState(false);
    const audioContextStartedRef = useRef(false);
    const currentNoteIndexRef = useRef(0);
    const [bpm, setBpm] = React.useState(50);

    useEffect(() => {
        let cleanupFunction;
        if (!audioContextStarted) {
            return
        }

        async function callPlayMelody() {
            cleanupFunction = await playMelody();
        }

        callPlayMelody().then(r => {
            return () => {
                setAudioContextStarted(false);
                if (cleanupFunction) {
                    cleanupFunction();
                }
            };
        });
    }, [audioContextStarted]);

    const startAudioContext = (seq) => {
        currentNoteIndexRef.current = 0;
        if (audioContextStartedRef.current) {
            setSequence(seq);
        } else {
            setSequence(seq);
            setAudioContextStarted(true);
            audioContextStartedRef.current = true;
        }
    };

    const stopMelody = async () => {
        setAudioContextStarted(false);
        audioContextStartedRef.current = false;
        setCurrentNoteIndex(-1);
    };

    const handleAudioPause = (audio) => {
        if (!audio) return;

        const audioContext = new (window.AudioContext || window.webkitAudioContext)();
        const gainNode = audioContext.createGain();
        const mediaElement = audio;

        const initialVolume = audioContext.currentTime;
        const fadeDuration = 0.4; // Time in seconds to fade out

        gainNode.gain.setValueAtTime(1, audioContext.currentTime);
        gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + fadeDuration);

        mediaElement.onended = () => {
            // Cleanup the AudioContext and GainNode
            audioContext.close();
            gainNode.disconnect();
        };

        mediaElement.onpause = () => {
            // Pause the media after the fade-out is complete
            mediaElement.pause();
        };

        // Connect the GainNode to the media element source
        const source = audioContext.createMediaElementSource(mediaElement);
        source.connect(gainNode);
        gainNode.connect(audioContext.destination);
    };


    const playMelody = () => {
        const currentSequence = songs[sequence];
        setCurrentNoteIndex(0);
        let previousPlayer = null;
        const playNote = () => {
            if (!audioContextStartedRef.current) {
                return;
            }

            if (previousPlayer) {
                handleAudioPause(previousPlayer)
            }
            let player = new Audio(
                `${process.env.PUBLIC_URL}/sounds/guitar/${getStoredChordName(currentSequence[currentNoteIndexRef.current])}.mp3`
            );
            setTimeout(() => {
                player.volume = 0.60;
                previousPlayer = player;
                player.play().then(r => {});
                setCurrentNoteIndex(currentSequence.indexOf(currentSequence[currentNoteIndexRef.current]));
                currentNoteIndexRef.current = (currentNoteIndexRef.current + 1) % currentSequence.length;
                setTimeout(playNote, (60 / bpm) * 1000 - pauseBetweenNotes);
            }, pauseBetweenNotes);
        };

        playNote();

        // Return a cleanup function to stop the current melody
        return () => {
            audioContextStartedRef.current = false;
        };
    };

    return (
        <Container>
            <div style={{ marginBottom: '16px' }}>
                <Button onClick={() => startAudioContext(sequence)} variant="contained">
                    Play
                </Button>
            </div>
            <div style={{ marginBottom: '16px' }}>
                <Button onClick={stopMelody} variant="contained">
                    Stop
                </Button>
            </div>
            <div style={{ marginBottom: '16px' }}>
                <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={sequence}
                    label="Song"
                    onChange={(e) => startAudioContext(e.target.value)}
                >
                    {Object.keys(songs).map((key) => (
                        <MenuItem value={key}>{key}</MenuItem>
                    ))}
                </Select>
            </div>
            <div style={{ marginBottom: '16px' }}>
                <Typography color="primary" fontSize="15px">
                    BPM: {bpm}
                </Typography>
                <Slider value={bpm}
                        onChange={(e, newValue) => setBpm(newValue)}
                        step={1}
                        min={0}
                        max={150}
                        disabled={audioContextStarted}
                />
            </div>
        </Container>

    );
};

export default MusicPlayer;
